.headComponent {
  display: flex;
  justify-content: space-between;

  height: 50px;
  padding: 0 20px;

  border-bottom: 1px solid rgb(60, 60, 255);
  box-shadow: 0px 0px 2px rgb(60, 60, 255);
}

.rectangle {
  height: 30px;
  width: 30px;
  border-radius: 150px;
  background-color: black;
}

.infoUser {
  display: flex;
  align-items: center;
}

.label {
  display: flex;
  align-items: center;
}
